import React, { useState, useEffect, Fragment } from "react"
import { Link as RouterLink, useParams, useNavigate, useSearchParams } from 'react-router-dom';
import { Box, TextField, Divider, Select, MenuItem, Skeleton, IconButton } from "@mui/material";
import Button from '@mui/material/Button'
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import Grid from "@mui/material/Unstable_Grid2";
import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";
import FormControlLabel from "@mui/material/FormControlLabel";
import { withAuthenticationRequired } from "@auth0/auth0-react";
import Checkbox from "@mui/material/Checkbox";
import UtterancesComments from "../components/UtterancesComments";
import { GAMESERVER_BACKEND_URL } from "../components/config";

const StoryChain = withAuthenticationRequired(ProtectedStoryChain)

function ProtectedStoryChain() {
  const [loading, setLoading] = useState(false)
  const [loaded, setLoaded] = useState(false)
  const [errMessage, setErrMessage] = useState('')
  const [story, setStory] = useState()
  const [storyId, setStoryId] = useState()
  const [title, setTitle] = useState()
  const [characters, setCharacters] = useState("Oscar, Sacha, and a boy named Marceau")
  const [antagonist, setAntagonist] = useState("Motorcycle Monster")
  const [contagonist, setContagonist] = useState("Mister Claw, a clumsy, spooky monster with mustard-colored claws")
  const [language, setLanguage] = useState("English")
  const [ignoreCache, setIgnoreCache] = useState(false)
  const params = useParams()
  const [queryString] = useSearchParams()
  const navigate = useNavigate()
  const [startTime, setStartTime] = useState()


  // if we received a content_id, bypass the form construction and retrieve that
  useEffect(() => {
    const queryContentId = params.contentId
    if (queryContentId) {
      console.log("Query string specifies content ID " + queryContentId)
      getStoryFromContentId(queryContentId)
    } else {
      console.log("No content_id specified in query string")
    }
    // eslint-disable-next-line -- empty [] intentional to make this RUNONCE
  }, [])


  // when the story gets updated (with contents from the async call)
  // then we want to finally set loaded=true
  useEffect(() => {
    console.log("in the story.useEffect hook")
    if (story) {
      setLoading(false)
      setLoaded(true)
      document.title = "GoPlayfully - " + title
    }
  }, [story])

  function onGenerateClick() {
    setStartTime(Date.now())
    setLoading(true)
    setLoaded(false)
    setErrMessage('')
    getStoryFromForm();
  }

  function randomizeFields() {
    let tempChar = characters
    let tempAnt = antagonist
    let tempCont = contagonist

    setAntagonist(tempChar)
    setContagonist(tempAnt)
    setCharacters(tempCont)
  }

  function SkeletonRows({value}) {
    const numRows = Math.random() * 4 + 2;
    return (
      <Grid item xs={12} sx={{mb:2}}>
        {Array.apply(null, {length: numRows}).map((text, index) => (
          <Skeleton key={index} animation="wave"
            variant="text" sx={{ fontSize: '1rem' }} width={(80 + Math.random() * 20)+"%"} />
        ))}
      </Grid>
    )
  }

  // main render loop
  return (
    <Grid container spacing={2}>
      <Grid item xs={2}>
        <Select
          id="language-select"
          name="Language"
          value={language}
          disabled={loading}
          label="Language"
          onChange={(event) => setLanguage(event.target.value)}
        >
          {[["English", "English"], ["Español", "Spanish"], ["Français", "French"]].map((text, index) => (
            <MenuItem key={text[0]} value={text[1]}>
              {text[0]}
            </MenuItem>
          ))}
        </Select>
      </Grid>
      <Grid item xs={12}>
        <TextField
          required
          disabled={loading}
          fullWidth
          multiline
          id="characters"
          label="Characters"
          value={characters}
          onChange={(event) => setCharacters(event.target.value)}
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          required
          fullWidth
          disabled={loading}
          multiline
          id="antagonist"
          value={antagonist}
          label="Antagonist"
          onChange={(event) => setAntagonist(event.target.value)}
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          required
          fullWidth
          disabled={loading}
          multiline
          value={contagonist}
          id="contagonist"
          label="Contagonist"
          onChange={(event) => setContagonist(event.target.value)}
        />
      </Grid>
      <Box width="100%" />
      <Grid item xs={5}>
        <Button variant="contained" onClick={randomizeFields}>
          Randomize
        </Button>
        &nbsp;
        <Button variant="contained" onClick={onGenerateClick}>
          Go
        </Button>
      </Grid>
      <Grid item xs={4}>
        <FormControlLabel
          control={
            <Checkbox
              checked={ignoreCache}
              onChange={(event) => setIgnoreCache(event.target.checked)}
            />
          }
          label="Ignore cache"
        />
      </Grid>
      <Box width="100%" />
      {loaded ? (
        <Fragment>
          <Grid item xs={12}>
            <Divider>Story</Divider>
          </Grid>
          <Grid item xs={12} sx={{ minwidth: 0 }}>
            <Typography variant="h1" sx={{ fontSize: "10vw" }}>
              {title}
            </Typography>
          </Grid>
          <Box width="100%" />
          <Grid item xs={12}>
            <Typography variant="body1" sx={{ whiteSpace: "pre-line" }}>
              {story}
            </Typography>
          </Grid>
          <Box width="100%" />
          <Grid item xs={4}>
            <Link component={RouterLink} to={"/storychain/" + storyId + "/"}>
              permalink
            </Link>
          </Grid>
          <Grid item xs={6}></Grid>
          <Grid item xs={1}>
            <IconButton aria-label="thumbs up" onClick={handleThumbsUp}>
              <ThumbUpIcon />
            </IconButton>
          </Grid>
          <Grid item xs={1}>
            <IconButton aria-label="thumbs down" onClick={handleThumbsDown}>
              <ThumbDownIcon />
            </IconButton>
          </Grid>
          <Box width="100%" />
          <Grid item xs={12}>
            <UtterancesComments
              repo="goplayfully/comments"
              issueTerm={params.contentId}
              theme={"photon-dark"}
              />
          </Grid>
        </Fragment>
      ) : (
        <Fragment>
          {errMessage ? (
            <Typography variant="body1" color="error.main">
              {errMessage}
            </Typography>
          ) : (
            <Fragment />
          )}
        </Fragment>
      )}
      {loading ? (
        <Fragment>
          {Array.apply(null, { length: 5 }).map((text, index) => (
            <SkeletonRows key={index} />
          ))}
        </Fragment>
      ) : (
        <Fragment />
      )}
    </Grid>
  );


  // fetch a story from the form fields
  function getStoryFromForm() {
    let storyURL = `${GAMESERVER_BACKEND_URL}storylist?characters=${characters}&contagonist=${contagonist}&antagonist=${antagonist}&language=${language}`
    if (queryString.get('forceFresh') || ignoreCache === true) {
      storyURL += '&force_fresh=1'
      console.log('Parameter `forceFresh` is true, so overriding any cached story')
    }
    if (queryString.get('maxSteps')) {
      storyURL += '&max_steps=' + queryString.get('maxSteps')
      console.log('Parameter `maxSteps` is true so requesting a max of ' + queryString.get('maxSteps'))
    }
    storyURL = encodeURI(storyURL)
    console.log(`Fetching storychain from ${storyURL}`);
    fetch(storyURL)
      .then(response => {
        console.log(`${storyURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        console.log("Fetched story... processing");
        // add timestamp to ensure a re-render in case we get exact same story (from cache)
        const d = new Date();
        setStoryId(data["content_id"])
        const slug = `\n\n(retrieved @ ${d.toISOString()})`
        setStory(data["story"] + slug)
        setTitle(data["title"])
        console.log(data)
        navigate("/storychain/" + data["content_id"], { replace: true })
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve story from ${storyURL}; ${error}`)
        setLoading(false)
        setLoaded(false)
        setStory('')
        setErrMessage('No story found with that ID')
      });
  }

  // fetch a story from the specified content ID in the query string
  function getStoryFromContentId(contentId) {
    setStory("")
    let storyURL = `${GAMESERVER_BACKEND_URL}storylist/${contentId}`
    console.log(`Fetching cached storychain from ${storyURL}`);
    fetch(storyURL)
      .then(response => {
        console.log(`${storyURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        console.log("Fetched story... processing");
        // add timestamp to ensure a re-render in case we get exact same story (from cache)
        const d = new Date();
        setStoryId(data["content_id"])
        // no need to Navigate because the URL is already set
        const slug = `\n\n(retrieved @ ${d.toISOString()})`
        setStory(data["story"] + slug)
        setTitle(data["title"])
        setLanguage(data["story_request"]["language"])
        setCharacters(data["story_request"]["characters"])
        setAntagonist(data["story_request"]["antagonist"])
        setContagonist(data["story_request"]["contagonist"])
        console.log(data)
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve story from ${storyURL}; ${error}`)
        setLoading(false)
        setLoaded(false)
        setStory('')
        setErrMessage('No story found with that ID')
      });
  }

  // process a thumbs up (positive) feedback click
  function handleThumbsUp() {
    handleThumbs("thumbsUp")
  }

  // process a thumbs down (negative) feedback click
  function handleThumbsDown() {
    handleThumbs("thumbsDown")
  }

  // process any feedback clicks
  function handleThumbs(value) {
    console.log("Submitting feedback of " + value)
    fetch(`${GAMESERVER_BACKEND_URL}storylist/${storyId}`, {
      method: 'POST',
        body: JSON.stringify({
          "feedback_type": value,
          "note": "",
        }),
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
        },
    })
    .then((response) => response.json())
    .then((data) => {
        console.log(data);
        // Handle data
    })
    .catch((err) => {
        console.log(err.message);
    });
  }
}

export default StoryChain;