import React, { useState, useEffect, Fragment } from "react"
import Backdrop from '@mui/material/Backdrop';
import Button from '@mui/material/Button'
import Container from '@mui/material/Container'
import LinearProgress from '@mui/material/LinearProgress';
import Link from "@mui/material/Link";
import { Link as RouterLink, useParams, useNavigate } from 'react-router-dom';
import Grid from "@mui/material/Unstable_Grid2";
import Collapse from "@mui/material/Collapse";
import Typography from "@mui/material/Typography";
import QuizIcon from '@mui/icons-material/Quiz';
import UtterancesComments from "../components/UtterancesComments";
import { GAMESERVER_BACKEND_URL } from "../components/config";

function Itinerary() {
  const [location, setLocation] = useState({"lat":0,"lon":0})
  const [narrative, setNarrative] = useState("")
  const [loading, setLoading] = useState(false)
  const [loaded, setLoaded] = useState(false)
  const [contentId, setContentId] = useState()
  const [showAnswer, setShowAnswer] = useState({})
  const [errMessage, setErrMessage] = useState()
  const params = useParams()
  const navigate = useNavigate()
  let runOnce = true

  // if we received a content_id, bypass the form construction and retrieve that
  useEffect(() => {
    if (runOnce) {
      runOnce = false
      console.log('in the runOnce useEffect hook')
      const queryContentId = params.contentId
      if (queryContentId) {
        runOnce = false
        console.log("Query string specifies content ID " + queryContentId)
        getItineraryFromContentId(queryContentId)
      } else {
        console.log("No content_id specified in URL")
      }
    }
    // eslint-disable-next-line -- empty [] intentional to make this RUNONCE
  }, [])

  const options = {
    enableHighAccuracy: true,
    timeout: 6000,
    maximumAge: 0,
  };

  // when the points variable gets updated (with contents from the async call)
  // then we want to finally set loaded=true
  useEffect(() => {
    if (narrative) {
      console.log("in the points.useEffect hook")
      setLoading(false)
      setLoaded(true)
    }
  }, [narrative])

  function getItineraryFromPosition(pos) {
    const crd = pos.coords;
    setLocation({"lat": crd.latitude, "lon": crd.longitude})

    console.log("Your current position is:");
    console.log(`Latitude : ${crd.latitude}`);
    console.log(`Longitude: ${crd.longitude}`);
    console.log(`More or less ${crd.accuracy} meters.`);

    let poiURL = `${GAMESERVER_BACKEND_URL}narrative/@${crd.latitude},${crd.longitude}?limit=3`
    console.log(`Fetching POI itinerary from ${poiURL}`);
    fetch(poiURL)
      .then(response => response.json())
      .then(data => {
        // Process the retrieved data
        console.log("FETCHED");
        // setPoints(data);
        setNarrative(data["itinerary"])
        setContentId(data["content_id"])
        navigate("/itinerary/" + data["content_id"], { replace: true })
        console.log(data);
      })
      .catch(error => {
        // Handle any errors
        setErrMessage(`Could not retrieve point of interest from ${poiURL}; ${error}`)
        console.error(`Could not retrieve point of interest from ${poiURL}; ${error}`)
        setLoading(false)
      });
  }

  function getItineraryFromContentId(contentId) {
    const itineraryURL = GAMESERVER_BACKEND_URL + `itinerary/${contentId}`
    console.log('Fetching Itinerary based on contentId from URL ' + itineraryURL)
    fetch(itineraryURL)
      .then(response => response.json())
      .then(data => {
        console.log('Fetched Itinerary')
        setNarrative(data["itinerary"])
        setContentId(data["content_id"])
        setLocation({"lat":data["lat"], "lon":data["lon"]})
        console.log(data)
      })
      .catch(error => {
        setErrMessage(`Could not retrieve itinerary from ${itineraryURL}; ${error}`)
        console.error(`Could not retrieve itinerary from ${itineraryURL}; ${error}`)
        setLoading(false)
      })
  }

  function error(err) {
    console.warn(`ERROR(${err.code}): ${err.message}`);
    setErrMessage('Could not retrieve location; please try again. '
      + err.message)
    setLoading(false)
  }

  function handleLocationClick() {
    setLoading(true)
    setNarrative('')
    setErrMessage('')
    navigator.geolocation.getCurrentPosition(getItineraryFromPosition, error, options);
  }

  const handleQuestIconClick = buttonId => e => {
    setShowAnswer(state => ({
      ...state,
      [buttonId]: !state[buttonId]
    }))
  }


  function QuestAnswer({value, index}) {
    return (
      <Fragment>
        &nbsp;
        <QuizIcon
          sx={{
            "&:hover": {
              cursor: "pointer",
            },
          }}
          color="primary"
          fontSize="small"
          onClick={handleQuestIconClick(index)}
        />
        <Collapse key={index} in={showAnswer[index]}>
          <Typography variant="body1" color="secondary.main">
            (Answer = {value})
          </Typography>
        </Collapse>
      </Fragment>
    );
  }

  function RenderItinerary({ value }) {
    if (!value) {
      return
    }
    console.log("RenderItinerary received value " + value.toString())
    let itinerary = undefined
    try {
      itinerary = JSON.parse(value)
    }
    catch (error) {
      console.error("Unparseable JSON: " + value)
      return
    }
    if (itinerary === undefined || Array.isArray(itinerary) === false) {
      console.error("RenderItinerary received mal-formed JSON: " + itinerary)
      return
    }
    let steps = itinerary.map((step, index) =>
        <li key={step}>
          <Typography variant="body1">
            {step.narrative}
          </Typography>
          <QuestAnswer value={step.answer} index={index} />
          <GoogleMapsDirectionsUrl lat={step.lat} lon={step.lon} />
        </li>
        )
    return (
      <Grid item xs={12}>
        <ol>
          {steps}
        </ol>
        {/* <GoogleMapsDirectionsUrl lat= lon=/> */}
      </Grid>
    )
  }


  function MultistepMap({value}) {
    if (!value) {
      console.error('MultistepMap no value')
      return
    }

    console.log('Rendering a MultiStepMap')
    const key = process.env.REACT_APP_GOOGLE_MAPS_API_KEY

    let itinerary = undefined
    try {
      itinerary = JSON.parse(value)
    }
    catch (error) {
      console.error("Unparseable JSON: " + value)
      return
    }
    if (itinerary === undefined || Array.isArray(itinerary) === false) {
      console.error("RenderItinerary received mal-formed JSON: " + itinerary)
      return
    }

    // itinerary contains an array of itinerary steps, each containing
    // a .lat and .lon parameter
    let waypointStr = ''
    for (let i=0; i < itinerary.length - 1; i++) {
      waypointStr += `${itinerary[i].lat},${itinerary[i].lon}`
      if (i + 1 < itinerary.length - 1) {
        waypointStr += '|'    // if there's more, add a pipe separator
      }
    }

    let lastWaypoint = itinerary[itinerary.length - 1]
    let lastWaypointStr = `${lastWaypoint.lat},${lastWaypoint.lon}`

    const mapURL =
      "https://www.google.com/maps/embed/v1/directions"
      + `?key=${key}`
      + `&origin=${location.lat},${location.lon}`
      + `&destination=${lastWaypointStr}`
      + `&waypoints=${waypointStr}`
      + "&avoid=tolls|highways"
      + "&mode=walking"

    console.log('Google Maps Embed URL: ' + mapURL)
    return (
      <iframe
        width="100%"
        height="50%"
        frameBorder="0"
        referrerPolicy="no-referrer-when-downgrade"
        src={mapURL}
      />
    )
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}><Typography variant="h1">Itinerary</Typography></Grid>
      <Grid item xs={12}>
      <Typography variant="body">Welcome to the Itinerary generator. To get
      started, you'll need to press the [Get Location] button and accept
      any browser requests for location permissions. Then the story generator
      should return a custom itinerary for your family to enjoy!</Typography>
      <br/><Typography variant="body" color="warning.main">Note that itinerary
      generation can take 20-30 seconds currently. Please be patient! &#x1F9D8;</Typography>
      </Grid>
      <Grid item xs={12}>
        {loading ? (
          <Backdrop
            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={loading}
          >
            <Container maxWidth="xs">
              <LinearProgress />
            </Container>
          </Backdrop>
        ) : (
          <Grid item xs={1}>
            <LocationButton onLocationButtonClick={handleLocationClick} />
          </Grid>
        )}
        <ErrorMessage value={errMessage} />
        {loaded && (
          <Fragment>
            <Grid item xs={12}>
              <LocationString value={location} />
            </Grid>
            <Grid item xs={12}>
              <RenderItinerary value={narrative} />
            </Grid>
            <Grid item xs={12}>
              <MultistepMap value={narrative}/>
            </Grid>
            <Grid item xs={12}>
              <UtterancesComments
                repo="goplayfully/comments"
                issueTerm={"itinerary/" + params.contentId}
                theme={"photon-dark"}
              />
          </Grid>
            <Grid item xs={12}>
              <DebugDump value={contentId} />
            </Grid>
          </Fragment>
        )}

    </Grid>
    </Grid>
  );
}

function ErrorMessage({ value }) {
  if (value) {
    return (
      <Typography variant="body2" color="error.main">{value}</Typography>
    )
  }
}

function LocationButton({ onLocationButtonClick }) {
  return (
    <Button variant="contained" onClick={onLocationButtonClick}>Get Location</Button>
  );
}

export function LocationString({value}) {
  return (
    <Typography variant="body2" color="info.main">Building a narrative for lat/lon ({value.lat}, {value.lon})</Typography>
  )
}

function GoogleMapsDirectionsUrl({lat, lon}) {
  let mapsBaseURL = "https://www.google.com/maps/dir/?"

  let params = new URLSearchParams()
  params.append('api', 1)
  params.append('travelmode', 'walking')
  params.append('destination', `${lat},${lon}`)

  let mapsURL = mapsBaseURL + params.toString()
  return (
    <Link href={mapsURL} target="_blank" rel="noopener noreferrer">(directions)</Link>
  )
}

function DebugDump({value}) {
  return (
    <Grid item xs={4}>
      <Link
        component={RouterLink}
        to={"/itinerary/" + value + "/"}
        >permalink</Link>
    </Grid>
  )
}


export default Itinerary;