import { Outlet, Link, useOutlet} from "react-router-dom";
import * as React from 'react';
import { useState } from "react";
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import { List, ListItemButton, ListItemText } from "@mui/material";
import Drawer from "@mui/material/Drawer";

function Nav() {
  // Find the current route object
  const outlet = useOutlet();

  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  // TODO: Access BrowserRouter to read these from the source of truth
  const routes = ["/", "itinerary", "story", "storychain", "index"];

  const open = true;
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={() => setIsDrawerOpen(true)}
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, textTransform: 'capitalize' }}>
            {outlet?.props?.children?.props?.match?.pathname?.slice(1)}
          </Typography>
          <Drawer open={isDrawerOpen} onClose={() => setIsDrawerOpen(false)}>
            <List>
              {['Home', 'Itinerary', 'Story', 'Story Chain', '(Future)'].map((text, index) => (
                <ListItemButton
                  key={text}
                  component={Link} // <-- pass Link as component
                  to={routes[index]}
                  onClick={() => setIsDrawerOpen(false)}
                  sx={{
                    minHeight: 48,
                    justifyContent: open ? 'initial' : 'center',
                    px: 2.5,
                  }}
                >
                  <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
                </ListItemButton>
              ))}
            </List>
        </Drawer>
        </Toolbar>
      </AppBar>
      <Box sx={{p: 2}}>
        <Outlet />
      </Box>
      <Box sx={{justifyContent: 'center', display: 'flex'}}>
        <Typography variant="caption" sx={{ color: 'info.main' }}>
          &copy; 2023 - Playfully
        </Typography>
      </Box>
    </Box>
  );
};

export default Nav;