import React, { useState, useEffect, Fragment } from "react"
import { Grid, Typography } from "@mui/material";
import LoginButton from "../login";
import LogoutButton from "../logout";
import ModeEditIcon from '@mui/icons-material/ModeEdit';
import IconButton from "@mui/material/IconButton";
import Profile from "../profile";
import Link from "@mui/material/Link";
import Skeleton, {SkeletonTheme} from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
import FeedbackDialog from "../components/FeedbackDialog";
import {Link as RouterLink} from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";
import { GAMESERVER_BACKEND_URL } from "../components/config";

function formatDate(ISOString) {
  let d = new Date(Date.parse(ISOString));
  return d.toString();
}

function Home() {
  // constants
  const [stories, setStories] = useState([])
  const { isAuthenticated } = useAuth0();

  useEffect(() => {
    getStoryList();
    // eslint-disable-next-line -- empty [] intentional to make this RUNONCE
  }, [])

  useEffect(() => {
    if (stories) {
      console.log('got stories in stories.useEffect hook')
    }
  }, [stories])

  function handleEdit(oldTitle, contentId) {
    let newTitle = prompt("Enter new title", oldTitle)
    if (newTitle !== null && newTitle !== oldTitle) {
      const updateURL = `${GAMESERVER_BACKEND_URL}story/${contentId}/title`
      console.log('editing title with ' + updateURL)
      fetch(updateURL, {
        method: "PUT",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({"title": newTitle})
      })
      .then(response => {
        console.log(`${updateURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        console.log("Updated title... processing");
        // need to make a copy of the array so React notices the change
        let storiesCopy = [...stories]
        const storyIndex = storiesCopy.findIndex((object) => object.content_id === contentId)
        storiesCopy[storyIndex].title = newTitle
        setStories(storiesCopy)   // force a re-render
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not update Title from ${updateURL}; ${error}`)
      });
    }
  }


  function Stories() {
    return (
      <Grid item xs={12}>
        <ul>
          {stories.length === 0 ? (
            <SkeletonTheme baseColor="#202020" highlightColor="#444">
              <Skeleton count={9.8} enableAnimation={true} />
            </SkeletonTheme>
          ) : (
            stories.map((story, index) => {
              return (
                <li key={story.content_id}>
                  <Link
                    component={RouterLink}
                    key={index}
                    to={"/story/" + story.content_id}
                  >
                    {story.title}
                  </Link>
                  <IconButton
                    aria-label="edit title"
                    size="small"
                    onClick={() => handleEdit(story.title, story.content_id)}
                  >
                    <ModeEditIcon fontSize="inherit" />
                  </IconButton>
                  <span>({formatDate(story.timestamp)})</span>
                </li>
              );
            })
          )}
        </ul>
      </Grid>
    );
  }


  const feedbackCallback = (title, feedback) => {
    console.log(title);
    console.log(feedback);
  }

  return (
    <Grid container sx={{maxWidth: 4/5}}>
      <Grid item xs={12}>
        <Typography variant="h1">Home</Typography>
      </Grid>
      <Grid item xs={12}>
        <Typography variant="body1" sx={{mb: 2}}>Welcome to the Playfully Gameserver test UI.
        This is a simple framework for displaying
        the responses from the Playfully backend. It's not intended for production,
        just for testing new prompts.
        </Typography>
        <Typography variant="body1" sx={{mb: 2}}>
        Use the menu in the upper-left to run a new generation task, or choose
        from the following pre-generated stories:
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <Stories />
      </Grid>
      <Grid item xs={12}>
        {isAuthenticated ? (
          <LogoutButton></LogoutButton>
        ): (
          <LoginButton></LoginButton>
        )}
      </Grid>
      <Grid item xs={12}><Profile>Name</Profile></Grid>
      {/* <Grid item xs={6}><FeedbackDialog feedbackCallback={feedbackCallback}/></Grid> */}
    </Grid>
  )

  // fetch list of stories
  function getStoryList() {
    let storyURL = `${GAMESERVER_BACKEND_URL}storylist`
    console.log(`Fetching storylist from ${storyURL}`);
    fetch(storyURL)
      .then(response => {
        console.log(`${storyURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        console.log("Fetched list... processing");
        console.log(data)
        setStories(data)
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve story from ${storyURL}; ${error}`)
      });
  }
};


export default Home;
