// src/components/utterances-comments.js
// from https://paulie.dev/posts/2022/08/how-to-use-utterances-with-react/
import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';

const UtterancesComments = ({ repo, theme, issueTerm }) => {
  const ref = useRef();

  useEffect(() => {
    console.log("In the UtterancesComments useEffect hook (should be runOnce")

    // check if we already ran
    if (ref.current.key) {
      console.log("Already have a #myUtterances tag")
      return;
    }
    else {
      ref.current.key = "1"
    }

    const script = document.createElement('script');

    const config = {
      src: 'https://utteranc.es/client.js',
      repo: repo,
      'issue-term': issueTerm,
      theme: theme,
      crossOrigin: 'anonymous',
      defer: true,
      id: "#myUtterances",
    };

    Object.entries(config).forEach(([key, value]) => {
      script.setAttribute(key, value);
    });

    setTimeout(() => {
      ref.current.append(script);
    }, 300);
  }, [repo, theme, issueTerm]);

  return <div ref={ref} />;
};

UtterancesComments.defaultProps = {
  theme: 'github-light',
  issueTerm: 'pathname'
};

UtterancesComments.propTypes = {
  /** The name of the public repository to open issues  */
  repo: PropTypes.string.isRequired,
  /** The type of Theme to use */
  theme: PropTypes.oneOf([
    'github-light',
    'github-dark',
    'preferred-color-scheme',
    'github-dark-orange',
    'icy-dark',
    'dark-blue',
    'photon-dark',
    'boxy-light',
    'gruvbox-dark'
  ]),
  /** The property to use as the issue title */
  issueTerm: PropTypes.string
};

export default UtterancesComments;